body {
  margin: 0;
  padding: 0 0 40px;
  font-family: sans-serif;
}
.App{text-align: center;}
.Main {display: -ms-flexbox;display: flex; width: 100%; }
.Slot{border:1px solid #ddd; padding: 20px; -ms-flex-preferred-size: 0; flex-basis: 0; -ms-flex-positive: 1; flex-grow: 1;}
header{background: #000; color:#fff; padding: 1px 0;}
footer{background: #666; color:#fff; padding: 10px 0; position: fixed; bottom: 0; left: 0; right: 0; }
.listing{ list-style: none; margin: 0; padding: 0; }
.listing li{ margin: 5px 0; padding: 8px 15px; display: block; border: 1px solid #eee; border-radius: 5px; font-size: 14px; cursor: pointer; }
.listing li.current, .listing li:hover{ background: #eee; color: #000; }
.info{font-size: 14px; text-align: left}
.info dt{font-weight: bold;}
.info dd{margin: 5px 0 10px;}



